<template>
  <div class="amain">
    <music-head :headwidth="660">
      <span slot="head">我的歌手 ({{artistSubList.count}})</span>
    </music-head>

    <div class="aritem" v-for="(value, index) in artistSubList.data" :key="index">
      <img :src="value.picUrl" @click="jumpPage('singerdetail', value.id)"/>
      <div class="arinfo">
        <div class="arname">
          <span @click="jumpPage('singerdetail', value.id)">{{value.name}}</span>
          <span>{{value.trans}}</span>
        </div>
        <div class="size">
          <span>{{value.albumSize}}个专辑</span>
          <span>{{value.mvSize}}个MV</span>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { request } from "../../network/request";
import MusicHead from "../Operate/MusicHead";
export default {
  name: "MyArtist",
  components: {
    MusicHead,
  },
  data() {
    return {
      artistSubList: {},
    };
  },
  created() {
    this.getArtistSubList();
  },
  methods: {
    getArtistSubList() {
      return request({
        url: "/artist/sublist",
      }).then((response) => {
        this.artistSubList = response;
      });
    },
    jumpPage(path, id){
      this.$router.push({
        name: path,
        params: {
          id: id
        }
      })
    }
  },
};
</script>

<style>
.size {
  color: #666;
}
.size>span:first-of-type {
  margin-right: 10px;
}
.arname > span:first-of-type {
  font-size: 16px;
  color: #333;
  font-weight: bold;
  margin-right: 5px;
  line-height: 40px;
  cursor: pointer;
}
.arname > span:first-of-type:hover {
  border-bottom: solid 1px;
}
.arname > span:last-of-type {
  color: #999;
}

.arinfo {
  margin-left: 20px;
  display: flex;
  flex-direction: column;
}
.aritem > img {
  width: 80px;
  height: 80px;
  cursor: pointer;
}
.aritem {
  display: flex;
  margin: 10px 40px;
  border-bottom: solid 1px #ddd;
  padding-bottom: 10px;
  font-size: 12px;
}
</style>